import React, { createRef } from 'react';
import PointerTracker from 'pointer-tracker';

import './style.scss';

class Cross extends React.Component {

    state = {
        orientation: ''
    };

    _positionH = 0;
    _relativePositionH = 0.5;
    _positionOnPointerStartH = 0;

    _positionHS = 0;
    _relativePositionHS = 0.75;
    _positionOnPointerStartHS = 0;

    _positionV = 0;
    _relativePositionV = 0.5;
    _positionOnPointerStartV = 0;

    _positionVS = 0;
    _relativePositionVS = 0.75;
    _positionOnPointerStartVS = 0;

    constructor(props) {
        super(props);

        this.container = createRef();
        this.horizontal = createRef();
        this.vertical = createRef();
        this.scrubber1 = createRef();
        this.scrubber2 = createRef();
    }

    componentDidMount() {
        const slider1 = this.scrubber1.current;
        const slider2 = this.scrubber2.current;

        const pointerTrackerH = new PointerTracker(slider1, {
            start: (_, event) => {
                if (pointerTrackerH.currentPointers.length === 1) return false;
                event.preventDefault();
                this._positionOnPointerStartH = this._positionH;
                this._positionOnPointerStartHS = this._positionHS;
                return true;
            },
            move: () => {
                this._pointerChangeH(
                    pointerTrackerH.startPointers[0],
                    pointerTrackerH.currentPointers[0]
                );
            }
        });

        const pointerTrackerV = new PointerTracker(slider2, {
            start: (_, event) => {
                if (pointerTrackerV.currentPointers.length === 1) return false;
                event.preventDefault();
                this._positionOnPointerStartV = this._positionV;
                this._positionOnPointerStartVS = this._positionVS;
                return true;
            },
            move: () => {
                this._pointerChangeV(
                    pointerTrackerV.startPointers[0],
                    pointerTrackerV.currentPointers[0],
                );
            }
        });

        if ('ResizeObserver' in window && this.container.current) {
            new ResizeObserver(() => this._resetPosition())
                .observe(this.container.current);
        } else {
            window.addEventListener('resize', this._resetPosition);
        }

        this._resetPosition();
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._resetPosition);
    }

    _resetPosition() {
        // Set the initial position of the handle.
        requestAnimationFrame(() => {
            if (this.container.current) {
                const bounds = this.container.current.getBoundingClientRect();
                this._positionH = bounds.height * this._relativePositionH;
                this._positionHS = bounds.width * this._relativePositionHS;
                this._positionV = bounds.width * this._relativePositionV;
                this._positionVS = bounds.height * this._relativePositionVS;
                this._setHorizontal();
                this._setHorizontalScrubber();
                this._setVertical();
                this._setVerticalScrubber();
            }
        });
      }

    _pointerChangeH(startPoint, currentPoint) {
        const bounds = this.container.current.getBoundingClientRect();
    
        this._positionHS = this._positionOnPointerStartHS +
            (currentPoint.clientX -startPoint.clientX);
        this._positionHS = Math.max(0, Math.min(this._positionHS, bounds.width - 30));
        this._relativePositionHS = this._positionHS / bounds.width;
        this._setHorizontalScrubber();

        this._positionH = this._positionOnPointerStartH +
            (currentPoint.clientY - startPoint.clientY);
        // Clamp position to element bounds.
        this._positionH = Math.max(0, Math.min(this._positionH, bounds.height));
        this._relativePositionH = this._positionH / bounds.height;
        this._setHorizontal();
        
    }

    _pointerChangeV(startPoint, currentPoint) {
        const bounds = this.container.current.getBoundingClientRect();
    
        this._positionVS = this._positionOnPointerStartVS +
            (currentPoint.clientY -startPoint.clientY);
        this._positionVS = Math.max(0, Math.min(this._positionVS, bounds.height - 30));
        this._relativePositionVS = this._positionVS / bounds.height;
        this._setVerticalScrubber();

        this._positionV = this._positionOnPointerStartV +
            (currentPoint.clientX - startPoint.clientX);
        // Clamp position to element bounds.
        this._positionV = Math.max(0, Math.min(this._positionV, bounds.width));
        this._relativePositionH = this._positionV / bounds.width;
        this._setVertical();
        
    }

    _setHorizontal = () => {
        if (this.horizontal.current) {
            this.horizontal.current.style.transform = `translateY(${this._positionH}px)`;
            this.props.setY && this.props.setY(this._positionH);
        }
    }

    _setHorizontalScrubber = () => {
        if (this.scrubber1.current) {
            this.scrubber1.current.style.transform = `translate(${this._positionHS}px, -50%)`;
        }
    }

    _setVertical = () => {
        if (this.vertical.current) {
            this.vertical.current.style.transform = `translateX(${this._positionV}px)`;
            this.props.setX && this.props.setX(this._positionV);
        }
    }

    _setVerticalScrubber = () => {
        if (this.scrubber2.current) {
            this.scrubber2.current.style.transform = `translate(-50%, ${this._positionVS}px)`;
        }
    }

    render() {
        const btn = (
            <svg className="icon" aria-hidden="true">
                <use xlinkHref="#icon-shangxiahuakuai" />
            </svg>
        );
        return (
            <div ref={this.container} className="panel">
                {this.props.children}
                <div
                    className="horizontal"
                    ref={this.horizontal}
                >
                    <div className="scrubber" ref={this.scrubber1}>
                        { btn }
                    </div>
                </div>
                <div
                    className="vertical"
                    ref={this.vertical}
                >
                    <div className="scrubber" ref={this.scrubber2}>
                        { btn }
                    </div>
                </div>
            </div>
        );
    }
}

export default Cross;
