import React from 'react';

import { nativeDecode, blobToText, blobToImg, drawableToImageData } from "@src/lib/util";
import Panel from '../panel';
import Result from '../result';

import './style.scss';

const originalDocumentTitle = document.title;

async function processSvg(blob) {
    const parser = new DOMParser();
    const text = await blobToText(blob);
    const document = parser.parseFromString(text, 'image/svg+xml');
    const svg = document.documentElement;
  
    if (svg.hasAttribute('width') && svg.hasAttribute('height')) {
      return blobToImg(blob);
    }
  
    const viewBox = svg.getAttribute('viewBox');
    if (viewBox === null) throw Error('SVG must have width/height or viewBox');
  
    const viewboxParts = viewBox.split(/\s+/);
    svg.setAttribute('width', viewboxParts[2]);
    svg.setAttribute('height', viewboxParts[3]);
  
    const serializer = new XMLSerializer();
    const newSource = serializer.serializeToString(document);
    return blobToImg(new Blob([newSource], { type: 'image/svg+xml' }));
}

class Pick extends React.Component {
    widthQuery = window.matchMedia('(max-width: 599px)');

    state = {
        source: {},
        mobileView: this.widthQuery.matches,
        result: [], // [0, 0, 0, 1]
    };

    constructor(props) {
        super(props);
        this._updateFile(props.file);
    }

    _onMobileWidthChange = () => {
        this.setState({ mobileView: this.widthQuery.matches });
    }

    componentDidMount() {
        this.widthQuery.addListener(this._onMobileWidthChange);
    }

    UNSAFE_componentWillReceiveProps(nextProps) {
        if (nextProps.file !== this.props.file) {
            this.updateFile(nextProps.file);
        }
    }

    updateDocumentTitle(filename = '') {
        document.title = filename ? `${filename} - ${originalDocumentTitle}` : originalDocumentTitle;
    }

    _updateFile = async file => {
        this.setState({ loading: true });
        try {
            let decoded;
            let vectorImage;
            if (file.type.startsWith('image/svg+xml')) {
                vectorImage = await processSvg(file);
                decoded = drawableToImageData(vectorImage);
            } else {
                decoded = await nativeDecode(file);
            }
            this.updateDocumentTitle(file.name);
            const newState = {
                ...this.state,
                source: {
                    decoded, file
                },
                loading: false
            };
            this.setState(newState);
        } catch (e) {
            console.error(e);
            alert('Invalid image');
            this.setState({ loading: false });
        }
    }

    render() {
        const { source, mobileView } = this.state;
        return (
            <div className="pick">
                <Panel
                    source={source}
                    mobileView={mobileView}
                    onBack={this.props.onBack}
                    onResult={result => this.setState({ result })}
                />
                <Result
                    rgba={this.state.result}
                />
            </div>
          );
    }
}

export default Pick;
