/* eslint-disable no-restricted-globals */
/* eslint-disable no-unused-expressions */
var deselectCurrent = require('toggle-selection');

var defaultMessage = 'Copy to clipboard: #{key}, Enter';

function format(message) {
    var copyKey = (/mac os x/i.test(navigator.userAgent) ? '⌘' : 'Ctrl') + '+C';
    return message.replace(/#{\s*key\s*}/g, copyKey);
}

export function copy(text, options) {
    var debug, message, reselectPrevious, range, selection, mark, success = false;
    if (!options) { options = {}; }
    debug = options.debug || false;
    try {
        reselectPrevious = deselectCurrent();

        range = document.createRange();
        selection = document.getSelection();

        mark = document.createElement('span');
        mark.textContent = text;
        // alert('链接已复制，现在可以邀请好友啦')
        // reset user styles for span element
        mark.style.all = 'unset';
        // prevents scrolling to the end of the page
        mark.style.position = 'fixed';
        mark.style.top = 0;
        mark.style.clip = 'rect(0, 0, 0, 0)';
        // used to preserve spaces and line breaks
        mark.style.whiteSpace = 'pre';
        // do not inherit user-select (it may be `none`)
        mark.style.webkitUserSelect = 'text';
        mark.style.MozUserSelect = 'text';
        mark.style.msUserSelect = 'text';
        mark.style.userSelect = 'text';

        document.body.appendChild(mark);

        range.selectNode(mark);
        selection.addRange(range);

        var successful = document.execCommand('copy');
        if (!successful) {
            throw new Error('copy command was unsuccessful');
        }
        success = true;
    } catch (err) {
        debug && console.error('unable to copy using execCommand: ', err);
        debug && console.warn('trying IE specific stuff');
        try {
            window.clipboardData.setData('text', text);
            success = true;
        } catch (err) {
            debug && console.error('unable to copy using clipboardData: ', err);
            debug && console.error('falling back to prompt');
            message = format('message' in options ? options.message : defaultMessage);
            window.prompt(message, text);
        }
    } finally {
        if (selection) {
            if (typeof selection.removeRange == 'function') {
                selection.removeRange(range);
            } else {
                selection.removeAllRanges();
            }
        }

        if (mark) {
            document.body.removeChild(mark);
        }
        reselectPrevious();
    }

    return success;
}


export function drawDataToCanvas(canvas, data) {
    const ctx = canvas.getContext('2d');
    if (!ctx) throw Error('Canvas not initialized');
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.putImageData(data, 0, 0);
}

async function decodeImage(url) {
    const img = new Image();
    img.decoding = 'async';
    img.src = url;
    const loaded = new Promise((resolve, reject) => {
      img.onload = () => resolve();
      img.onerror = () => reject(Error('Image loading error'));
    });
  
    if (img.decode) {
      // Nice off-thread way supported in Safari/Chrome.
      // Safari throws on decode if the source is SVG.
      // https://bugs.webkit.org/show_bug.cgi?id=188347
      await img.decode().catch(() => null);
    }
  
    // Always await loaded, as we may have bailed due to the Safari bug above.
    await loaded;
    return img;
}

export async function blobToImg(blob) {
    const url = URL.createObjectURL(blob);
  
    try {
      return await decodeImage(url);
    } finally {
      URL.revokeObjectURL(url);
    }
}

export function blobToText(blob) {
    return new Response(blob).text();
}

export function drawableToImageData(drawable, opts = {}) {
    const {
      width = drawable.width,
      height = drawable.height,
      sx = 0,
      sy = 0,
      sw = drawable.width,
      sh = drawable.height,
    } = opts;
  
    // Make canvas same size as image
    const canvas = document.createElement('canvas');
    canvas.width = width;
    canvas.height = height;
    // Draw image onto canvas
    const ctx = canvas.getContext('2d');
    if (!ctx) throw new Error('Could not create canvas context');
    ctx.drawImage(drawable, sx, sy, sw, sh, 0, 0, width, height);
    return ctx.getImageData(0, 0, width, height);
}

export async function nativeDecode(blob) {
    // Prefer createImageBitmap as it's the off-thread option for Firefox.
    const drawable = 'createImageBitmap' in self ?
      await createImageBitmap(blob) : await blobToImg(blob);
  
    return drawableToImageData(drawable);
}

/** Replace the contents of a canvas with the given data */
export function drawImageToCanvas(canvas, img) {
    const ctx = canvas.getContext('2d');
    if (!ctx) throw Error('Canvas not initialized');
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
}


export function zoom(target, source, x, y, offsetX, offsetY) {
    const sourceCtx = source.getContext('2d');
    const { width, height } = source;

    sourceCtx.imageSmoothingEnabled = false;
    sourceCtx.mozImageSmoothingEnabled = false;
    sourceCtx.webkitImageSmoothingEnabled = false;
    sourceCtx.msImageSmoothingEnabled = false;

    sourceCtx.drawImage(target,
        x - offsetX , y - offsetY,
        10, 10,
        0, 0,
        width, height);

        sourceCtx.beginPath();
        sourceCtx.strokeStyle = 'red';
        sourceCtx.moveTo(width/2 - 5, height/2 - 5);
        sourceCtx.lineTo(width/2 + 5, height/2 - 5);
        sourceCtx.lineTo(width/2 + 5, height/2 + 5);
        sourceCtx.lineTo(width/2 - 5, height/2 + 5);
        sourceCtx.lineTo(width/2 - 5, height/2 - 5);
        sourceCtx.stroke();
        sourceCtx.closePath();
}

export function pick(target) {
    const ctx = target.getContext('2d');
    const pixel = ctx.getImageData(target.width/2, target.height/2, 1, 1);
    const data = pixel.data;
    // const rgba = 'rgba(' + data[0] + ', ' + data[1] +
    //            ', ' + data[2] + ', ' + (data[3] / 255) + ')';
    // return rgba;
    return [data[0], data[1], data[2], data[3] / 255];
}
