import React, { createRef } from 'react';

import logo from '@assets/logo.svg';
import testpic from '@assets/testpic.jpg';

import './style.scss';

class Intro extends React.Component {
    state = {};

    constructor(props) {
        super(props);
        this.fileInput = createRef();
    }

    _resetFileInput = () => {
        this.fileInput.current && (this.fileInput.current.value = '');
    }

    _onButtonClick = () => {
        this.fileInput.current && (this.fileInput.current.click());
    }

    _onFileChange = e => {
        const fileInput = e.target;
        const file = fileInput.files && fileInput.files[0];
        if (!file) return;
        this._resetFileInput();
        this.props.onFile(file);
    }

    _onDemoClick = () => {
        console.log(testpic)
    }

    render() {
        return (
            <div className="intro">
                <img src={logo} className="App-logo" alt="logo" />
                <p className="open-image-guide">
                    <span> Drag &amp; drop or</span>
                    {' '}
                    <button
                        className="select-button"
                        onClick={this._onButtonClick}
                    >
                        select an image
                    </button>
                    <input
                        ref={this.fileInput}
                        style={{ display: 'none' }}
                        type="file"
                        accept="image/*"
                        onChange={this._onFileChange}
                    />
                </p>
                {/* <div className="demo-tip">Or try this</div>
                <div className="demo-pic" onClick={this._onDemoClick}>
                    <img src={testpic} alt='test' />
                </div> */}
                {/* <div>
                <input type='text' />
                <button>-></button>
                </div> */}
            </div>
          );
    }
}

export default Intro;
