import React, { Component, createRef } from 'react';

import FileDrop from 'react-file-drop';

import Intro from './component/intro';
import Pick from './component/pick';

import './App.scss';

const location = window.location;
const ROUTE_EDITOR = '/editor';

function back() {
    window.history.back();
}

class App extends Component {
    state = {
        isEditorOpen: false,
        file: undefined,
    };

    constructor() {
        super();

        document.body.addEventListener('gesturestart', (event) => {
            event.preventDefault();
        });
      
        window.addEventListener('popstate', this.onPopState);
    }

    _handleDrop = (files, event) => {
        if (!files || files.length === 0) return;
        const file = files[0];
        if (!/image/.test(file.type)) {
            console.log('invalid image');
            return;
        }
        this.openEditor();
        this.setState({ file });
    }

    _onPickeLocalFile = file => {
        if (!/image/.test(file.type)) {
            console.log('invalid image');
            return;
        }
        this.openEditor();
        this.setState({ file });
    }

    openEditor() {
        if (this.state.isEditorOpen) return;
        const editorURL = new URL(location.href);
        editorURL.pathname = ROUTE_EDITOR;
        window.history.pushState(null, '', editorURL.href);
        this.setState({ isEditorOpen: true });
    }

    onPopState = () => {
        this.setState({ isEditorOpen: location.pathname === ROUTE_EDITOR });
    }

    render() {
        return (
            <FileDrop onDrop={this._handleDrop}>
                {
                    this.state.isEditorOpen
                    ? <Pick file={this.state.file} onBack={back} />
                    : <Intro onFile={this._onPickeLocalFile} />
                }
            </FileDrop>
        );
    }
}

export default App;
