import React, { createRef } from 'react';

import { copy } from '@src/lib/util';
import { rgbToHsl, rgbToHex } from '@src/lib/color';

import './style.scss';

class Result extends React.Component {

    state = {
        expand: false
    };

    constructor(props) {
        super(props);
    }

    _toggleExpand = () => {
        this.setState({ expand: !this.state.expand });
    }

    copyContent(text) {
        if (copy(text)) {
            alert('copy success');
        };
    }

    render() {
        const { rgba = [] } = this.props;
        const { expand } = this.state;
        if (rgba.length < 1) return null;
        const rgbStr = `rgb (${rgba[0]}, ${rgba[1]}, ${rgba[2]})`;
        const hslStr = rgbToHsl(rgba[0], rgba[1], rgba[2]);
        const hexStr = rgbToHex(rgba[0], rgba[1], rgba[2]);
        const expandClass = expand ? 'active' : '';
        return (
            <div className="result">
                <div className={`result-main ${expandClass}`}>
                    <div className="color-item" onClick={() => this.copyContent(hexStr)}>
                        { hexStr }
                        <svg className="icon" aria-hidden="true">
                            <use xlinkHref="#icon-copy" />
                        </svg>
                    </div>
                    <div className="color-item" onClick={() => this.copyContent(hslStr)}>
                        { hslStr }
                        <svg className="icon" aria-hidden="true">
                            <use xlinkHref="#icon-copy" />
                        </svg>
                    </div>
                </div>
                <div className="result-bar">
                    <div className={`expand-btn ${expandClass}`} onClick={this._toggleExpand}>
                        <svg className="icon" aria-hidden="true">
                            <use xlinkHref="#icon-up" />
                        </svg>
                    </div>
                    <div className="preview-area" onClick={() => this.copyContent(rgbStr)}>
                        { rgbStr }
                        <svg className="icon" aria-hidden="true">
                            <use xlinkHref="#icon-copy" />
                        </svg>
                    </div>
                </div>
            </div>
        );
    }
}

export default Result;
