import React, { createRef } from 'react';

import PinchZoomPan from "react-responsive-pinch-zoom-pan";

import { drawDataToCanvas, zoom, pick } from '@src/lib/util';
import Cross from './cross';

import './style.scss';

let config = {
    attributes: true,
    childList: true,
    subtree: true
};

class Panel extends React.Component {

    state = {
        posX: 0,
        posY: 0,
        left: 0,
        top: 0,
        scale: 1,
        showMagnifier: true
    };

    _observer = new MutationObserver((mutationsList) => {
        for(let mutation of mutationsList) {
            let type = mutation.type;
            switch (type) {
                case "attributes":
                    if (this.canvas.current) {
                        let offsetWidth = this.canvas.current.width;
                        let rect = this.canvas.current.getBoundingClientRect();
                        let { left = 0, top = 0, width } = rect;
                        let scale = (width / offsetWidth );
                        this.setState({ left, top, scale }, () => this._drawMagnifier());
                    }
                    console.log(`The ${mutation.attributeName} attribute was modified.`);
                    break;
                default:
                    break;
            }
        }
    });

    constructor(props) {
        super(props);
        this.canvas = createRef();
        this.magnifier = createRef();
        this.magnifierGrid = createRef();
        this.pinchZoomPan = createRef();
    }

    componentDidMount() {
        const drawable = this.getDrawable();
        if (this.canvas.current && drawable) {
            drawDataToCanvas(this.canvas.current, drawable);
        }
    }

    componentDidUpdate(prevProps, prevState) {
        const prevDrawable = this.getDrawable(prevProps);
        const drawable = this.getDrawable();
    
        if (drawable && drawable !== prevDrawable && this.canvas.current) {
            drawDataToCanvas(this.canvas.current, drawable);
            this._observer.observe(this.canvas.current, config);
        }
    }

    // shouldComponentUpdate(nextProps, nextState) {
    //     return (nextProps.source.decoded !== this.props.source.decoded);
    // }

    componentWillUnmount() {
        this._observer.disconnect();
    }

    getDrawable(props = this.props) {
        return props.source && props.source.decoded;
    }

    _drawMagnifier = () => {
        if (this.canvas.current && this.magnifier.current) {
            const { posX, posY, left, top, scale } = this.state;
            zoom(
                this.canvas.current,
                this.magnifier.current,
                Math.round((posX - left) / scale),
                Math.round((posY - top) / scale),
                4.5,
                4.5
            );
            const rgba = pick(this.magnifier.current);
            this.props.onResult(rgba);
        }
    }

    _toggleMagnifier = () => {
        this.setState({
            showMagnifier: !this.state.showMagnifier
        })
    }

    render() {
        const drawable = this.getDrawable();
        if (!drawable) return null;
        const { showMagnifier } = this.state;

        const tools = (
            <div className="side-bar">
                <button className="item scale-btn">
                    { Math.round(this.state.scale * 100) }
                </button>
                <button className="item scale-btn active" onClick={this._toggleMagnifier}>
                    <svg className="icon" aria-hidden="true">
                        <use xlinkHref="#icon-radar" />
                    </svg>
                </button>
            </div>
        )
        return (
            <div className="panel-wrapper">

                <button className="back-btn btn" onClick={this.props.onBack}>
                    <svg className="icon" aria-hidden="true">
                        <use xlinkHref="#icon-back" />
                    </svg>
                </button>
                { tools }
                <Cross
                    setX={posX => this.setState({ posX }, () => this._drawMagnifier())}
                    setY={posY => this.setState({ posY }, () => this._drawMagnifier())}
                >
                    <PinchZoomPan
                        position='center'
                        zoomButtons={false}
                        ref={this.pinchZoomPan}
                    >
                        
                        <canvas
                            ref={this.canvas}
                            width={drawable.width}
                            height={drawable.height}
                        />
                    </PinchZoomPan>
                </Cross>
                <div className={`magnifier ${showMagnifier ? '' : 'hidden'}`}>
                    <canvas
                        ref={this.magnifier}
                        width={110}
                        height={110}
                    />
                </div>
            </div>
          );
    }
}

export default Panel;
